<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;getAllList();">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="用能对象名称">
          <el-input v-model="searchForm.formdata.name"></el-input>
        </el-form-item>

        <el-form-item label="用能对象类型">
          <el-select v-model="searchForm.formdata.type" filterable placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option v-for="item in orgTypeOptions" :key="item.identify" :label="item.identifyValue" :value="parseInt(item.identify)"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="270" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="getTransfer('orgMeter',scope.row)">关联表计</el-button>
            <el-button size="small" @click="getTransfer('orgProduct',scope.row)">关联产品</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @open="openDialog" @close="closeDialog('addForm')">
      <el-form label-width="120px" :model="addForm" :rules="rules" ref="addForm">

        <el-form-item label="用能对象名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="用能对象类型" prop="type">
          <el-select v-model="addForm.type" filterable placeholder="请选择">
            <el-option v-for="item in orgTypeOptions" :key="item.identify" :label="item.identifyValue" :value="parseInt(item.identify)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="上级部门" prop="parentId">
          <!-- <el-input auto-complete="off" v-model="addForm.parentId"></el-input> -->
          <da-select-tree v-model="addForm.parentId" id="orgTreeId" ref="orgTreeId" :data="entOrgData" :hide-node="orgHideNode" :props="{label:'name',children:'children'}" @change="selectParentNode"></da-select-tree>
        </el-form-item>
        <el-form-item label="显示顺序" prop="showSort">
          <el-input auto-complete="off" v-model="addForm.showSort"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
    <!-- 关联表计 -->
    <da-dialog title="关联表计" :visible.sync="orgMeterDialogVisible" size="570px">
      <relev-transfer ref="orgMeter" :visible.sync="orgMeterDialogVisible" :params-attr="orgId" :base-attr="meterAttr"></relev-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveTransferDialog('orgMeter')">确 定</el-button>
        <el-button @click="orgMeterDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
    <!-- 关联产品 -->
    <da-dialog title="关联产品" :visible.sync="orgProductDialogVisible" size="570px">
      <relev-transfer ref="orgProduct" :visible.sync="orgProductDialogVisible" :params-attr="orgId" :base-attr="productAttr"></relev-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveTransferDialog('orgProduct')">确 定</el-button>
        <el-button @click="orgProductDialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
import { getTreeByList } from "@/util/util.js";
import relevTransfer from "./template/relevProduct";
export default {
  name: "organization",
  components: { relevTransfer },
  data() {
    return {
      orgHideNode: "",
      orgTypeOptions: [],
      entOrgData: [],
      // 查询表单
      searchForm: {
        formdata: { name: "", type: "" },
        currentPage: 1,
        pageSize: 10
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "用能对象名称", prop: "name", align: "left", sortable: true },
        {
          label: "用能对象类型",
          prop: "type",
          displace: {},
          align: "left",
          sortable: true
        },
        {
          label: "上级部门",
          prop: "parentId",
          displace: {},
          align: "left",
          sortable: true
        },
        { label: "显示顺序", prop: "showSort", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: { name: "", type: "", parentId: "", showSort: "" },
      rules: this.eachValidate({
        name: { required: true, maxString: "50" },
        type: { required: { required: true, trigger: "change" } },
        parentId: { required: { required: true, trigger: "change" } },
        showSort: {}
      }),
      meterSelected: [],
      meterLists: [],

      orgId: "",
      // 关联表计
      orgMeterDialogVisible: false,
      meterAttr: {
        dataUrl: "rest/enterpriseOrg/orgMeter", // 获取数据的地址
        getPramsAttr: "orgId", // 获取数据的参数属性名
        saveUrl: "rest/enterpriseOrg/orgMeter", // 保存数据的地址
        valueKey: "meterId",
        label: "meterName",
        titles: ["未关联表计", "已关联表计"],
        isEditScale: true
      },
      // 关联产品
      orgProductDialogVisible: false,
      productAttr: {
        dataUrl: "rest/enterpriseOrg/equipProduct", // 获取数据的地址
        getPramsAttr: "orgId", // 获取数据的参数属性名
        saveUrl: "rest/enterpriseOrg/orgProduct", // 保存数据的地址
        valueKey: "productId",
        label: "productName",
        titles: ["未关联产品", "已关联产品"],
        isEditScale: false
      }
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑用能对象管理" : "新增用能对象管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/enterpriseOrg/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    getAllList(row) {
      let formName = "table";
      this.$ajax
        .get("rest/enterpriseOrg/page", {
          params: {
            formdata: { name: "", type: "" },
            currentPage: 1,
            pageSize: 9999
          },
          noLoading: true
        })
        .then(res => {
          if (res.data.status) {
            // if (!this.colsData[2].displace) {
            let data = {};
            this.$each(res.data.records, item => {
              data[item.id] = item.name;
            });
            this.colsData[2].displace = data;
            // }
            this.entOrgData = getTreeByList(res.data.records, "id", "parentId");
            // 编辑时把自身节点过滤掉
            if (row) {
              this.addForm = JSON.parse(JSON.stringify(row));
              //   remove
              this.orgHideNode = row.id;
            } else {
              this.orgHideNode = "";
            }
          }
        });
    },
    //
    getOrgType() {
      this.$ajax.get("rest/enterpriseOrg/orgType").then(res => {
        if (res.data.status) {
          this.orgTypeOptions = res.data.records;
          let toObj = {};
          this.$each(res.data.records, item => {
            toObj[item.identify] = item.identifyValue;
          });
          this.colsData[1].displace = toObj;
        }
      });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.getAllList(row);
    },
    openDialog() {
      this.getOrgType();
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = { name: "", type: "", parentId: "", showSort: "" };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/enterpriseOrg", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(
        () => {
          this.$ajax
            .delete("rest/enterpriseOrg", { params: { id: row.id } })
            .then(res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            });
        },
        () => {}
      );
    },
    selectParentNode(node, lastNode) {
      if (node.id === this.addForm.id) {
        this.$message.error("用能对象不能以自身作为父节点。");
        // this.addForm.parentId = "";
        this.$set(this.addForm, "parentId", lastNode.id);
      }
    },
    getTransfer(refName, row) {
      this.orgId = row.id;
      this[refName + "DialogVisible"] = true;
      console.log(refName, this.$refs[refName]);
      this.$nextTick(() => {
        this.$refs[refName].getData(row);
      });
    },
    saveTransferDialog(refName) {
      this.$refs[refName].saveData();
    }
  },
  mounted() {
    this.getOrgType();
    this.getAllList();
    this.getTbList();
  }
};
</script>



